.banner-bg {
	padding: 260px 80px;
	position: relative;
	background: url('../img/main_banner.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	z-index: 2;
}

.banner-overlay {
	position: absolute;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0, 0.8);
	top: 0;
	left: 0;
	z-index: 3;
}

.welcome-text {
	position: relative;
	z-index: 4;
	h2 {
		font-size: 42px;
		font-family: 'robotobold';
		color: #fff;
		margin-bottom: 30px;
	}
	h5 {
		font-size: 24px;
		color: white;
		font-family: Roboto-Thin;
	}
}

@include media(max-width $phone) {
	.banner-bg {
		padding: 60px 30px;
	}
}
