#projectsdetails {
	.projectCart {
		border: 1px solid darkslategray;
		padding: 10px 15px;
		border-radius: 4px;
		font-family: monospace;
		

		span {
			font-weight: bold;
		}

		h3, h4, h5 {
			color: $mywhite;
		}
		
		h3 {
			font-size: 22px;
			text-align: center;
			padding-bottom: 10px;
		}

		h4 {
			font-size: 18px;
			padding-bottom: 10px;
		}

		h5 {
			text-align: right;
			padding-bottom: 10px;
		}

		.linn {
			text-align: right;
		}

		.reviecomment {
			font-size: 18px;
			
			color: darkgoldenrod;
		}
	}
}